}
.prod_item .box .prod_img_cover img {
      width: 100%;
      min-height: 100%;
}
.prod_item .box .prod_img_cover:hover .cover {
      -webkit-transform: rotateX(0deg);
              transform: rotateX(0deg);
}
.prod_item .box .prod_info {
